.related-products {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;

  @media (min-width: @tablet-width) {
    min-width: 768px;
  }

  @media (min-width: @desktop-width) {
    min-width: 1440px;
  }
}

.related-products__title {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
  margin-top: 25px;
  margin-bottom: 34px;

  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: @button-back-color;
    top: 60%;
    z-index: -1;
  }

  @media (min-width: @tablet-width) {
    margin-top: 1px;
    margin-bottom: 80px;

    &::after {
      top: 62%;
    }
  }

  @media (min-width: @desktop-width) {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.related-products__title-inner-wrapper {
  display: block;
  width: 320px;
  margin: 0 auto;
  z-index: 1;

  @media (min-width: @tablet-width) {
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    width: 1300px;
  }
}

.related-products__title-name {
  font-family: @family-base;
  font-style: @style-base;
  font-weight: @weight-base;
  font-size: 24px;
  line-height: 24px;
  display: inline-block;
  vertical-align: baseline;
  background-color: @button-text-color;
  padding-right: 21px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    font-size: 36px;
    line-height: 36px;
    padding-right: 36px;
    padding-left: 37px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 35px;
    padding-left: 40px;
  }
}

.related-products__inner-wrapper {
  @media (min-width: @desktop-width) {
    width: 1440px;
    margin: 0 auto;
  }
}

.related-products__gift-container {
  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    width: 1218px;
    margin: 0 auto;
    padding: 80px 1px 86px 1px;
  }
}

.related-products__list {
  display: flex;
  flex-direction: column;
  width: 280px;
  list-style-type: none;
  padding: 0 @value;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 710px;
    padding: 0 29px;
  }

  @media (min-width: @desktop-width) {
    width: 895px;
    padding: 0;
    margin: 0;
  }
}

.related-products__item {
  width: 280px;
  border-bottom: 1px solid #cdcdcd;
  padding: 0;
  margin: 0;
  padding-bottom: 19px;

  @media (min-width: @tablet-width) {
    display: grid;
    width: 632px;
    grid-template-columns: 360px 241px;
    column-gap: 31px;
    row-gap: 21px;
    .padding-horizontal(38px);
    padding-bottom: 28px;
    border: 1px solid #cdcdcd;
  }

  @media (min-width: @desktop-width) {
    width: 895px;
    grid-template-columns: 286px 287px 244px;
    column-gap: 38px;
    padding: 0;
    border-left: none;
    border-right: none;
  }
}

.related-products__item:first-child {
  border-top: 1px solid #cdcdcd;
}

.related-products__item:not(:first-child) {
  @media (min-width: @tablet-width) {
    border-top: none;
  }
}

.related-products__link-name {
  text-decoration: none;
  font-family: inherit;
  font-size: 16px;
  line-height: 16px;
  color: @catalog-title-item;
  text-transform: uppercase;
  display: block;
  margin: 0;
  padding: 0;
  padding-top: 12px;
  padding-bottom: 13px;

  @media (min-width: @tablet-width) {
    font-size: @value;
    line-height: @value;
    margin: 0;
    padding-top: 27px;
    padding-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    padding-top: 25px;
    padding-bottom: 23px;
  }
}

.related-products__features-list {
  font-family: @family-optional;
  font-style: @style-base;
  font-weight: @weight-base;
  font-size: 14px;
  line-height: 14px;
  color: @paragraph-text-color;
  display: flex;
  width: 280px;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  padding-bottom: 15px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: @value;
    display: grid;
    grid-template-columns: 147px 59px;
    column-gap: 33px;
    width: 241px;
    padding-top: 28px;
    padding-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    width: 285px;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    padding-top: 28px;
    padding-bottom: 21px;
  }
}

.related-products__feature-term {
  @media (min-width: @tablet-width) {
    display: block;
    margin: 0;
    padding: 0;
    padding-bottom: 2px;
    text-align: end;
  }
}

.related-products__feature-description {
  @media (min-width: @tablet-width) {
    display: block;
    margin: 0;
    padding: 0;
    padding-right: 3px;
    text-align: end;
  }

  @media (min-width: @desktop-width) {
    padding-right: 0;

    &--min-price {
      padding-right: 9px;
    }
  }
}

.related-products__order-button {
  font-size: 16px;
  line-height: @value;
  display: block;
  text-align: center;
  width: 260px;
  padding: 10px;
  height: max-content;
  //margin-top: auto;
  //margin-bottom: auto;

  @media (min-width: @tablet-width) {
    grid-column: 1 / -1;
    width: 612px;
  }

  @media (min-width: @desktop-width) {
    grid-column: 3 / -1;
    width: 224px;
    align-self: center;
  }
}
